					v-flex(xs2)
						v-btn(icon)
							svg-icon(name="plus" size="25px" fill="#7094f7")
</template>
<script>
export default {
    props: {
        pageTitle: {
            type: String
		}
	},
    mounted() {
        this.$emit("passTitle", this.pageTitle);
    }
};
</script>